/* Base layout */
html, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  font-family: "Helvetica Neue", sans-serif;
  background: black;
  color: white;
}

/* Scroll container */
.container {
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.container::-webkit-scrollbar {
  display: none;
}

/* Top bar fixed */
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 3.5rem;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}

.logo {
  font-size: clamp(1rem, 2.5vw, 1.5rem);
  font-weight: bold;
}

.dot-xyz {
  font-size: clamp(0.8rem, 2vw, 1rem);
  color: #fe9971;
  font-weight: bold;
}

.category-select {
  background: #000;
  color: #fff;
  border: 1px solid #555;
  padding: 0.3rem 0.5rem;
  font-size: clamp(0.85rem, 3vw, 1rem);
}

/* Card layout */
.card {
  height: 100vh;
  scroll-snap-align: start;
  position: relative;
  background-size: cover !important;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #111;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

/* Card content */
.card-content {
  position: relative;
  margin: 0 auto;
  padding: 1.75rem 1.25rem 2.75rem;
  margin-bottom: clamp(2.5rem, 7vh, 3.75rem); /* creates visible margin from bottom */
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(10px);
  box-sizing: border-box;
  z-index: 2;
}

/* Inner content */
.card-content-inner {
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/* Headline */
h1 {
  font-size: clamp(1.25rem, 5vw, 2rem);
  margin-bottom: 0.5rem;
  font-weight: bold;
}

/* Summary */
p {
  font-size: clamp(0.9rem, 3.5vw, 1.05rem);
  line-height: 1.6;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}

/* Read more */
.read-more {
  margin-top: 0.8rem;
  color: #ccc;
  text-decoration: none;
  font-weight: bold;
  display: inline-block;
  font-size: clamp(0.85rem, 3vw, 0.95rem);
}

/* Loading */
.loading {
  margin-top: 6rem;
  padding: 2rem;
  font-size: 1.2rem;
  color: white;
  text-align: center;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.bookmark-btn {
  font-size: 2.5rem;
  background: none;
  border: none;
  color: #ccc;
  cursor: pointer;
  transition: color 0.2s ease, transform 0.2s ease;
  padding: 0;
  margin: 0;
}

.bookmark-btn:hover {
  color: #fff;
  transform: scale(1.1);
}

.bookmark-btn.active {
  color: #4ea1ff; /* blue highlight */
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(4px);
  z-index: 200;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 4rem;
  overflow-y: auto;
}

.modal {
  background: #111;
  color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  max-width: 720px;
  width: 90%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.modal-header h2 {
  font-size: 1.5rem;
  margin: 0;
}

.close-btn {
  font-size: 1.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.search-input {
  display: block;
  margin: 1rem auto;
  width: 80%;
  padding: 0.5rem 0.75rem;
  margin-bottom: 1.25rem;
  background: #222;
  color: white;
  border: 1px solid #444;
  border-radius: 0.4rem;
  font-size: 1rem;
}

.bookmark-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.bookmark-item {
  display: flex;
  gap: 1rem;
  background: #1a1a1a;
  border-radius: 0.5rem;
  padding: 1rem;
  align-items: center;
}

.bookmark-thumb {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 0.25rem;
  flex-shrink: 0;
}

.bookmark-info {
  flex: 1;
  overflow: hidden;
}

.bookmark-title {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 0.3rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.bookmark-desc {
  font-size: 0.9rem;
  color: #ccc;
  max-height: 3.5em;
  overflow: hidden;
  text-overflow: ellipsis;
}

.bookmark-remove {
  background: none;
  border: none;
  color: #888;
  font-size: 1.5rem;
  cursor: pointer;
  transition: color 0.2s ease;
}

.bookmark-remove:hover {
  color: #f44;
}

.empty {
  text-align: center;
  color: #777;
  padding: 1rem;
}

.top-controls {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.bookmark-open-btn {
  background: #000;
  border: 1px solid #555;
  color: #fff;
  font-size: 1.2rem;
  padding: 0.3rem 0.55rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: background 0.2s ease, border 0.2s ease;
}

.bookmark-open-btn:hover {
  background: #111;
  border-color: #888;
}